.home-section-1 {
    position: relative;
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
}

.div-tarjeta-1 {
    display: flex;
    justify-content: left;
    margin: 40px 20px 20px 40px;
    align-items: center;

}

.home-texto-1 h1 {
    display: flex;
    justify-content: right;
    position: relative;
    margin: 10px;
}

.home-texto-1 p {
    display: flex;
    justify-content: right;
    position: relative;
    margin: 10px;
}

@media screen and (max-width: 600px) {

    .home-section-1 {
        position: relative;
        width: 100%;
        height: auto;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 40px;
    }
    
    .div-tarjeta-1 {
        position: relative;
        display: flex;
        justify-content: right;
        margin: 40px 30px 10px 10px;
        align-items: center;
    
    }
    
    .home-texto-1 h1 {
        display: flex;
        justify-content: right;
        position: relative;
        margin: 20px;
    }
    
    .home-texto-1 p {
        display: flex;
        justify-content: right;
        position: relative;
        margin: 20px;
    }

}