.tarjeta {
    position: relative;
    background-color: rgb(0, 0, 0);
    width: 300px;
    height: 400px;
    justify-content: center;
    align-items: center;
    border-radius: 25px;
    box-shadow: 0 20px 20px 0 #000000bb;
    overflow: hidden; /* esto recorta la imagen dentro del contenedor */
}

div .tarjeta img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 300px;
    height: auto;
    border-radius: 25px;
    box-shadow: 0 20px 20px 0 #000000bb;
}



@media screen and (max-width: 600px) {

    div .tarjeta img {
        width: 150px;
        height: auto;
    }

    .tarjeta {
        position: relative;
        background-color: rgb(0, 0, 0);
        width: 150px;
        height: 200px;
        justify-content: center;
        align-items: center;
        border-radius: 25px;
        box-shadow: 0 20px 20px 0 #000000bb;
        overflow: hidden; /* esto recorta la imagen dentro del contenedor */
    }
    
    .div-tarjeta-1 {
        display: flex;
        justify-content: left;
        margin: 80px 20px 20px 40px;
        position: fixed;

    }

}